.bjui-dialog {
  .bjui-pageFooter {
    li {
      .btn {
        height: 26px;
        padding: 0 10px;
      }
    }
  }

  background: #fff;

  &.dialogShadow {
    @include box-shadow($shadow-3)
  }

  > .dialogHeader {

    a {
      color: #222 !important;

      &:hover {
        color: $coreColor !important;
      }
    }

    h1 {
      color: #222;
    }
  }

  > .dialogContent {
    margin: 0;
    overflow: auto;

    .bjui-pageHeader {
      margin-bottom: 0;
      border: none;
    }

    .bjui-pageContent {
      background-color: #fff;
    }

    .bjui-pageFooter {
      padding-bottom: 0;
      border: none;
    }
  }
}

/* Lookup */
.bjui-lookup {
  position: absolute;
  top: 1px;
  right: 1px;
  display: block;
  width: 32px;
  text-align: center;
}
